import { getSystemHealth, getRecentActions, getRecentAssets } from '@/lib/file-system';
import { HealthOrb } from '@/components/pulse/HealthOrb';
import { LiveFeed } from '@/components/pulse/LiveFeed';
import { RecentWins } from '@/components/pulse/RecentWins';
import { MetricsGrid } from '@/components/pulse/MetricsGrid';

export const dynamic = 'force-dynamic';

export default async function PulsePage() {
  // Fetch all data in parallel
  const [health, actions, assets] = await Promise.all([
    getSystemHealth(),
    getRecentActions(15),
    getRecentAssets(6),
  ]);

  return (
    <div className="space-y-8">
      {/* Header */}
      <div>
        <h1 className="text-3xl font-bold text-white">Mission Control</h1>
        <p className="text-white/60 mt-1">
          Real-time system overview and activity feed
        </p>
      </div>

      {/* Bento Grid Layout */}
      <div className="grid grid-cols-12 gap-6">
        {/* Health Orb - Large */}
        <div className="col-span-12 lg:col-span-4">
          <HealthOrb health={health} />
        </div>

        {/* Metrics Grid */}
        <div className="col-span-12 lg:col-span-8">
          <MetricsGrid assets={assets} health={health} />
        </div>

        {/* Live Feed */}
        <div className="col-span-12 lg:col-span-6">
          <LiveFeed initialActions={actions} />
        </div>

        {/* Recent Wins */}
        <div className="col-span-12 lg:col-span-6">
          <RecentWins assets={assets} />
        </div>
      </div>
    </div>
  );
}
